import Vue from 'vue'
import Connector from '@/utils/connector'
import {BigNumber} from 'bignumber.js'
import {CfxStake,ControlledToken, Erc20, PCFX, PeriodicPrizeStrategy, PrizePool, TokenFaucet,TokenFaucetProxyFactory,UniswapV2Pair} from "@/contracts";
import config from "@/config";
import {Conflux} from "js-conflux-sdk";

let auth

const web3 = {
    state: {
        account: null,
        balance: 0,
        pcfxBalance: 0,
        chainId: 0,

        ticketContract: null,
        pcfxContract: null,
        prizePoolContract: null,
        periodicPrizeStrategyContract: null,
        poolGoContract: null,
        tokenFaucetContract: null,
        cfxStakeContract:null,
        cfxUniswapV2PairContract:null,
        pcfxPrice:1,
        poolgoPrice:2,
        confluxReader:null,
        isPortalInstalled:false,
    },
    mutations: {
        UPDATE_CHAIN_ID: (state, chainId) => {
            state.chainId = chainId
        },
        HANDLE_ACCOUNTS_CHANGED: (state, account) => {
            state.account = account
        },
        UPDATE_BALANCE: (state, balance) => {
            state.balance = balance
        },
        UPDATE_PCFX_BALANCE: (state, pcfxBalance) => {
            state.pcfxBalance = pcfxBalance
        },
        INIT_TICKET_CONTRACT:(state,ticketContract)=>{
            state.ticketContract = ticketContract
        },
        INIT_PCFX_CONTRACT:(state,pcfxContract)=>{
            state.pcfxContract = pcfxContract
        },
        INIT_PRIZE_POOL_CONTRACT:(state,prizePoolContract)=>{
            state.prizePoolContract = prizePoolContract
        },
        INIT_PERIOD_PRIZE_STRATEGY_CONTRACT:(state,periodicPrizeStrategyContract)=>{
            state.periodicPrizeStrategyContract = periodicPrizeStrategyContract
        },
        INIT_POOLGO_CONTRACT:(state,poolGoContract)=>{
            state.poolGoContract = poolGoContract
        },
        INIT_TOKEN_FAUCET_CONTRACT:(state,tokenFaucetContract)=>{
            state.tokenFaucetContract = tokenFaucetContract
        },
        INIT_CFX_STAKE_CONTRACT:(state,cfxStakeContract)=>{
            state.cfxStakeContract = cfxStakeContract
        },
        INIT_TOKEN_FAUCET_PROXY_FACTORY_CONTRACT:(state,tokenFaucetProxyFactoryContract)=>{
            state.tokenFaucetProxyFactoryContract = tokenFaucetProxyFactoryContract
        },

        INIT_CFX_UNISWAPV2_PAIR_CONTRACT:(state,cfxUniswapV2PairContract)=>{
            state.cfxUniswapV2PairContract = cfxUniswapV2PairContract
        },
        INIT_PCFX_PRICE:(state,pcfxPrice)=>{
            state.pcfxPrice = pcfxPrice
        },
        INIT_POOLGO_PRICE:(state,poolgoPrice)=>{
            state.poolgoPrice = poolgoPrice
        },
        INIT_CONFLUX_READER:(state,confluxReader)=>{
            state.confluxReader = confluxReader
        },
        UPDATE_IS_PORTAL_INSTALLED:(state,isPortalInstalled)=>{
            state.isPortalInstalled = isPortalInstalled
        },
    },
    actions: {
        async init({commit, dispatch}) {
            let confluxReader = new Conflux({
                url: config.web3Provider,
                networkId: config.networkId
            });
            let ticket = new ControlledToken(
                config.ticketAddress
            );
            let pcfx = new PCFX(
                config.pcfxAddress
            );
            let prizePool = new PrizePool(
                config.prizePoolAddress
            );
            let periodicPrizeStrategy = new PeriodicPrizeStrategy(
                config.periodicPrizeStrategyAddress
            );
            let poolGo = new Erc20(
                config.poolGoAddress
            );
            let tokenFaucet = new TokenFaucet(
                config.tokenFaucetAddress
            );
            let cfxStatke = new CfxStake(
                config.cfxStakeAddress
            );
            let tokenFaucetProxyFactory = new TokenFaucetProxyFactory(
                config.tokenFaucetProxyFactoryAddress
            );
            let cfxUniswapV2Pair = new UniswapV2Pair(
                config.cfxUniswapV2PairAddress
            );
            commit('INIT_CONFLUX_READER', confluxReader);
            commit('INIT_TICKET_CONTRACT', ticket);
            commit('INIT_PCFX_CONTRACT', pcfx);
            commit('INIT_PRIZE_POOL_CONTRACT', prizePool);
            commit('INIT_PERIOD_PRIZE_STRATEGY_CONTRACT', periodicPrizeStrategy);
            commit('INIT_POOLGO_CONTRACT', poolGo);
            commit('INIT_TOKEN_FAUCET_CONTRACT', tokenFaucet);
            commit('INIT_CFX_STAKE_CONTRACT', cfxStatke);
            commit('INIT_TOKEN_FAUCET_PROXY_FACTORY_CONTRACT', tokenFaucetProxyFactory);
            commit('INIT_CFX_UNISWAPV2_PAIR_CONTRACT', cfxUniswapV2Pair);
            let price = await cfxUniswapV2Pair.getPrice();
            commit('INIT_PCFX_PRICE', price);
            commit('INIT_POOLGO_PRICE', 2);
        },
        async cfxLogin({commit, dispatch}) {
            await dispatch('login', 'CFX');
        },
        async logout({commit, dispatch}) {

            commit('UPDATE_BALANCE', 0)
            commit('HANDLE_ACCOUNTS_CHANGED', null)
        },
        async login({commit, dispatch}, connectorType) {
            const connector = new Connector(connectorType)
            if (connector.isLoggedIn()) {
                commit('UPDATE_IS_PORTAL_INSTALLED', true)
                auth = await connector.connect()
                if (connectorType == 'ETH') {
                    // auth.web3 = new Web3Provider(auth)
                } else {
                    auth.conflux = window.confluxJS;
                    // console.log(auth.conflux.networkVersion)
                    // console.log(auth.conflux.selectedAddress)

                }

                await dispatch('loadProvider', connectorType)
            }else{
                commit('UPDATE_IS_PORTAL_INSTALLED', false)

            }
        },
        async loadProvider({commit, dispatch}, connectorType) {
            try {
                if (auth.removeAllListeners) {
                    auth.removeAllListeners()
                }

                if (auth.on) {
                    auth.on('chainChanged', async chainId => {
                        commit('UPDATE_CHAIN_ID', parseInt(formatUnits(chainId, 0)))
                    })

                    auth.on('accountsChanged', async accounts => {
                        if (accounts.length !== 0) {
                            commit('HANDLE_ACCOUNTS_CHANGED', accounts[0]);
                            await dispatch('loadProvider',connectorType)
                        }else{
                            commit('HANDLE_ACCOUNTS_CHANGED', null);
                        }
                    })

                    auth.on('disconnect', async () => {
                        commit('HANDLE_ACCOUNTS_CHANGED', null);
                        commit('HANDLE_CLOSE')
                    })
                }

                let accounts
                if (connectorType == 'ETH') {
                    accounts = await auth.request({method: "eth_requestAccounts"})
                    var account = accounts.length > 0 ? accounts[0] : null
                    commit('HANDLE_ACCOUNTS_CHANGED', account)

                    var chainId = parseInt(auth.chainId)
                    commit('UPDATE_CHAIN_ID', chainId)

                    var balance = await auth.web3.getBalance(account)
                    commit('UPDATE_BALANCE', balance / 10 ** 18)
                } else if (connectorType == 'CFX') {
                    var account = await auth.selectedAddress;
                    // var account = accounts.length > 0 ? accounts[0] : null

                    var chainId = parseInt(auth.chainId)
                    commit('UPDATE_CHAIN_ID', chainId)
                    // console.log("chainId");
                    // console.log(chainId);
                    // if (chainId != parseInt(config.networkId)){
                    // }
                    if (account != null) {
                        commit('HANDLE_ACCOUNTS_CHANGED', account)
                        console.log(account);
                        let balance = await auth.conflux.getBalance(account)
                        // console.log(balance);
                        commit('UPDATE_BALANCE', BigNumber(balance).div(BigNumber(10 ** 18)))
                        let pcfxBalance = await web3.state.pcfxContract.balanceOf(account);
                        commit('UPDATE_PCFX_BALANCE', pcfxBalance);
                    }
                }

            } catch (e) {
                //commit('LOAD_PROVIDER_FAILURE', e)
                console.log(e);
            }
        }
    }
}

export default web3